@import url(https://fonts.googleapis.com/css?family=Libre+Franklin:400,700i&display=swap);
* {
  box-sizing: border-box;
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: "Libre Franklin", sans-serif;
  --primary-color: #00203fff;
  --secondary-color: #adefd1ff;
  --primary-text-color: white;
}

.NavBar-nav-container {
  display: flex;
  justify-content: space-between;
  background: var(--primary-color);

  padding: 1.1rem;
  position: -webkit-sticky;
  position: sticky;
  height: 8vh;
}
.NavBar-nav-container a {
  text-decoration: none;
  color: var(--secondary-color);
  font-weight: 900;
  text-shadow: 0px 0px 1px;
  transition: all 0.5s ease-in-out;
}
.NavBar-nav-link-active {
  color: var(--primary-text-color) !important;
  text-shadow: none !important;
}
.NavBar-nav-left {
  display: flex;
  list-style: none;
  margin-left: 0.7rem;
}

.NavBar-nav-left a,
.NavBar-nav-right a {
  margin-left: 0.7rem;
}

.NavBar-nav-right-user-signed {
  display: flex;
}
.NavBar-user-profile-img {
  width: 25px;
  height: 25px;
  border-radius: 50%;
}

.NavBar-nav-profile-active {
  position: relative;
  top: -0.2rem;
  border-radius: 50%;
  border: 2px solid white;
}

.recommender-btn {
  color: white;
  cursor: pointer;
}

.recommender-btn-enable {
  color: var(--secondary-color) !important;
}

.Home-container {
  width: 100vw;
}

.friend-container {
  display: flex;
  width: 70vw;
  margin: 1rem 0rem;
  padding: 10px;
  overflow-y: scroll;
  justify-content: flex-start;
}

.friend-image img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
}
.friend-single {
  margin: 0.8rem;
  padding: 10px;
  border-radius: 6px;
  transition: all 0.4s ease-in;
  box-shadow: 0px 0px 4px -2px var(--primary-color) inset;
}

.friend-single:hover {
  opacity: 0.5;
}
.friend-container:hover > .friend-single,
.friends-container:hover > .friend-single {
  opacity: 0.5;
}

.friend-container:hover > .friend-single:hover,
.friends-container:hover > .friend-single:hover {
  opacity: 1;
  box-shadow: 1px 1px 13px -2px var(--primary-color);
}

.friend-username {
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 1rem;
}

.btn-add {
  background-color: var(--primary-color);
  color: var(--primary-text-color);
  padding: 6px;
  border-radius: 8px;
}

.btn-cancel {
  padding: 2px;
  border-radius: 8px;
}
.friend-footer {
  display: flex;
  justify-content: space-evenly;
}

.post-content-container {
  width: 54vw;
  height: 8vh;
  font-size: 0.8rem;
  padding: 10px;
  outline: none;
  border: none;
}

.post-content-container:focus,
.post-content-container:hover {
  border: 2px solid var(--primary-color);
  outline: none;
}
.post-title:focus,
.post-title:hover {
  border-bottom: 2px solid var(--primary-color);
}
.option-category {
  text-transform: uppercase;
}
.create-post {
  margin-bottom: 2rem;
}
.create-post-label {
  background-color: #f5f6f7;
  padding: 10px;
  transition: all 0.4s ease-in;
}
.create-post-active {
  background-color: var(--primary-color) !important;
  color: var(--primary-text-color) !important;
  font-feature-settings: "c2sc", 1.2rem;
  font-variant: small-caps;
  -webkit-font-kerning: 1.2rem;
          font-kerning: 1.2rem;
}
.post-body {
  transition: all 1s ease-in;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.post-title {
  padding: 5px;
  outline: none;
  border: none;
}
.post-category {
  padding: 10px;
  height: 2.4rem;
  border: none;
}
.post-category:hover,
.post-category:focus {
  outline: none;
  border: none;
  background-color: var(--primary-color);
  color: var(--primary-text-color);
}
.post-create {
  background-color: #f5f6f7;
  color: black;
  padding: 10px;
  font-size: 0.7rem;
  text-transform: uppercase;
  cursor: pointer;
}

.post-create:focus,
.post-create:hover {
  background-color: var(--primary-color);
  color: var(--primary-text-color);
  outline: none;
}

.Posts-container {
  margin-bottom: 2rem;
}

.loader {
  height: 3px;
  width: 0;
  -webkit-animation: loader-loading 2s ease-in-out infinite alternate;
          animation: loader-loading 2s ease-in-out infinite alternate;
  background-color: var(--secondary-color);
}

@-webkit-keyframes loader-loading {
  from {
    width: 0;
  }
  to {
    width: 100vw;
  }
}

@keyframes loader-loading {
  from {
    width: 0;
  }
  to {
    width: 100vw;
  }
}

.Post-container {
  width: 45vw;
  padding: 1rem;
  border: 1px solid var(--primary-color);
  border-radius: 8px;
  box-shadow: 0px 0px 12px -7px var(--primary-color) inset;
  margin-bottom: 2.5rem;
}

.Post-title {
  font-size: 1.8rem;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  margin-bottom: 0.8rem;
}

.Post-label {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  font-size: 0.7rem;
  margin-bottom: 1rem;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  position: relative;
}
.Post-user,
.Post-cat {
  color: darkblue;
  position: relative;
  cursor: pointer;
}
.Post-cat:hover::after {
  content: "";
  position: absolute;
  width: 0%;
  bottom: -7%;
  left: 3%;
  border: 1px solid var(--primary-color);
  -webkit-animation: border-in 1s ease-in-out forwards;
          animation: border-in 1s ease-in-out forwards;
}
.Post-footer {
  display: flex;
  justify-content: space-around;
  margin-top: 1.2rem;
  position: relative;
}

.post-stat-image {
  background: white;
  position: absolute;
  width: 40px;
  top: -0.3rem;
  z-index: 4;
}

.post-stat-image:hover {
  transform: scale(0.8);
}

.hash-tag {
  font-size: 1.8rem;
  position: relative;
  top: 0.4rem;
}

.read-more-tag {
  white-space: pre-line;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  font-size: 0.7rem;
  cursor: pointer;
  margin-left: 1rem;
  margin-top: 0.8rem;
  color: #f03901;
}

.Footer-container {
  height: 8vh;
  background-color: var(--primary-color);
  padding: 1.1rem;
  box-shadow: 0 50vh 0 50vh var(--primary-color);
  color: var(--secondary-color);
}

.Error-code {
  font-size: 16rem;
}

.Error-message {
  font-size: 2rem;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
}

.SignUp-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem;
  height: 80vh;
  justify-content: center;
}

.SignUp-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-shadow: 7px 5px 20px -8px black, 20px -10px 0 0 var(--primary-color);
  border-radius: 8px;
  padding: 0.8125rem;
}
.SignUp-form-input-field {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 0.8125rem;
  border-radius: 3px;
}

.SignUp-form-input-field label {
  font-weight: 900;
  font-size: 0.6rem;
  text-transform: uppercase;
  color: #c5c5c5;
  margin-bottom: 0.2rem;
}

.SignUp-form-input-field input {
  height: 4vh;
  border: none;
  box-shadow: 2px 2px 6px 2px inset rgb(241, 241, 241);
  background-color: #fdfdfd;
  text-align: center;
}
.SignUp-form-input-field input::-webkit-input-placeholder {
  text-align: center;
  color: #c5c5c5;
  font-size: 0.8rem;
}
.SignUp-form-input-field input:-ms-input-placeholder {
  text-align: center;
  color: #c5c5c5;
  font-size: 0.8rem;
}
.SignUp-form-input-field input::-ms-input-placeholder {
  text-align: center;
  color: #c5c5c5;
  font-size: 0.8rem;
}
.SignUp-form-input-field input::placeholder {
  text-align: center;
  color: #c5c5c5;
  font-size: 0.8rem;
}

.SignUp-form-button {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 20px;
}

.SignUp-form-header {
  font-size: 2rem;
  position: relative;
}

.SignUp-form-header::after {
  content: "";
  border: 0.4px solid var(--secondary-color);
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

.SignUp-form-button input {
  background-color: var(--primary-color);
  color: var(--primary-text-color);
  border: none;
  outline: none;
  padding: 0.6rem 0.8rem;
  font-size: 0.8rem;
  text-transform: uppercase;
  border-radius: 0.3rem;
  transition: all 0.3s ease-in-out;
}

.SignUp-form-button input:hover,
.SignUp-form-button input:focus {
  letter-spacing: 0.4rem;
  cursor: pointer;
}

.ServerResponse-container {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 10vh;
  transform: translate(40vw, -20vh);
  background-color: ghostwhite;
  opacity: 0;
  border-radius: 8px;
  -webkit-animation: message-fade-in 1s ease-in-out forwards;
          animation: message-fade-in 1s ease-in-out forwards;
}

.ServerResponse-container span {
  position: absolute;
  top: 0.4rem;
  right: 0.5rem;
}

@-webkit-keyframes message-fade-in {
  to {
    transform: translate(40vw, 30vh);
    opacity: 1;
  }
}

@keyframes message-fade-in {
  to {
    transform: translate(40vw, 30vh);
    opacity: 1;
  }
}

.Interest-select-item-container {
  margin: 2rem 17rem;
  display: grid;
  grid-template-columns: repeat(3, 33%);
  justify-items: center;
}
.Interest-select-item-img {
  width: 200px;
  height: 100px;
}

.Interest-select-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.Interest-select-item p {
  font-family: Arial, Helvetica, sans-serif;
  text-transform: capitalize;
  padding: 0.3rem;
}
.Interest-select-item-selected {
  opacity: 0.3;
}

.Interest-arrow-next-page {
  z-index: 2;
  position: fixed;
  cursor: pointer;
  right: 0;
  top: 40%;
  animation: arrow-in-out 1s ease-in-out infinite alternate-reverse;
}
.Interest-arrow-previous-page {
  z-index: 2;
  position: fixed;
  cursor: pointer;
  left: 0;
  top: 40%;
  transform: rotate(180deg);
  animation: arrow-in 1s ease-in-out infinite 1s alternate-reverse;
}
@-webkit-keyframes arrow-in {
  to {
    transform: rotate(180deg) translateX(2rem) scale(0.5);
  }
}
@keyframes arrow-in {
  to {
    transform: rotate(180deg) translateX(2rem) scale(0.5);
  }
}
@-webkit-keyframes arrow-in-out {
  to {
    transform: scale(0.5) translateX(2rem);
  }
}
@keyframes arrow-in-out {
  to {
    transform: scale(0.5) translateX(2rem);
  }
}

.Interest-select-item-heading-container {
  text-align: center;
}
.Interest-select-item-heading {
  padding: 1.3rem;
  font-size: 2.4rem;
  text-transform: capitalize;
}
.Interest-select-item-sub-heading {
  font-size: 0.8rem;
  margin-bottom: 2rem;
  opacity: 0.7;
}

.ProfileUpdate-img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
}

.ProfileUpdate {
  align-items: center !important;
}

.ProfileUpdate-img-upload-container {
  margin: 1rem;
  position: relative;
}

input[type="file"],
.label-update-image {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  text-align: center;
  cursor: pointer;
  color: var(--primary-text-color);
  font-size: 900;
  background-color: var(--primary-color);
  padding: 10px;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
  transition: all ease-in-out 1s;
}
Input[type="file"] {
  visibility: hidden;
}

input[type="file"]:hover,
.label-update-image:hover {
  opacity: 0;
}

.Me-container {
  /* height: 80vh; */
  width: 100vw;
  display: grid;
  overflow: scroll;
  grid-template-areas: "side main";
}

.Me-side {
  width: 15vw;
  background-color: var(--secondary-color);
  overflow: scroll;
  height: 100%;
  position: relative;
  grid-area: side;
}

.Me-main {
  width: 80vw;
  grid-area: main;
}

.Me-side-bar-section {
  padding: 1.8rem;
}

.Me-side-bar-section-title {
  font-size: 1.4rem;
  font-weight: 900;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
}

.Me-side-bar-sub-section div {
  padding-top: 0.6rem;
  padding-left: 0.3rem;
  position: relative;
  transition: color, font-size ease-in-out 0.3s;
}

.Me-side-bar-sub-section div:hover::after {
  content: "";
  position: absolute;
  width: 0%;
  bottom: -7%;
  left: 3%;
  border: 1px solid var(--primary-color);
  -webkit-animation: border-in 1s ease-in-out forwards;
          animation: border-in 1s ease-in-out forwards;
}

@-webkit-keyframes border-in {
  to {
    width: 70%;
  }
}

@keyframes border-in {
  to {
    width: 70%;
  }
}

.Me-side-bar-sub-section-active {
  font-size: 1.3rem;
  color: white !important;
  border-bottom: 4px solid var(--primary-color);
}

.page {
  display: flex;
  align-items: center;
  min-height: 80vh;
  flex-direction: column;
  opacity: 0;
  -webkit-animation: fade-in ease-in 1s forwards;
          animation: fade-in ease-in 1s forwards;
}

@-webkit-keyframes fade-in {
  to {
    opacity: 1;
  }
}

@keyframes fade-in {
  to {
    opacity: 1;
  }
}

.Profile-image-me {
  border-radius: 50%;
  border: 5px solid var(--primary-color);
}

.MeSection-container {
  margin-top: 1.5rem;
}

.Profile-username-me {
  font-size: 1.2rem;
  font-weight: 900;
  color: var(--primary-color);
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  margin-top: 1.2rem;
  text-align: center;
}

.Profile-image-me-container {
  display: flex;
  justify-content: center;
}

.Profile-username-dashboard-stats-container {
  display: flex;
  justify-content: center;
  background-color: var(--primary-color);
  margin: 1.2rem;
  border-top-left-radius: 25%;
  border-top-right-radius: 25%;
  padding-top: 20px;
  box-shadow: 0px 8px 10px 5px var(--secondary-color);
}
.Profile-username-dashboard-stats-container div {
  padding: 0.5rem 1.2rem;
  color: var(--secondary-color);
}
.Profile-username-dashboard-stats-container div div {
  text-align: center;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
}
.dashboard-dislikes {
