}

.dashboard-top-posts-header {
  text-align: center;
  color: var(--primary-color);
  font-size: 700;
  font-size: 2rem;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  text-decoration: underline;
}
.dashboard-top-posts-container {
  margin: 1.8rem 0;
}

.dashboard-post-stats-container {
  display: flex;
  justify-content: space-around;
}
.dashboard-post-container {
  display: flex;
  width: 80vw;
  justify-content: space-evenly;
  margin-bottom: 3.3rem;
}
.dashboard-post {
  width: 200px;
  padding: 20px;
  border-radius: 3px;
  opacity: 1;
  box-shadow: 1px 2px 3px 2px var(--primary-color);
  transition: all 0.3s ease-in;
