  margin-bottom: 0.8rem;
}

.dashboard-stats-title img {
  width: 15px;
}
.dashboard-post-stats {
  display: flex;
}

.dashboard-stats-content {
  margin-left: 0.3rem;
}

.dashboard-post:hover {
  opacity: 0.5;
}

.dashboard-post-container:hover > .dashboard-post {
  opacity: 0.5;
}
.dashboard-post-container:hover > .dashboard-post:hover {
  opacity: 1;
  transform: scale(1.1);
}

.top-interests {
  position: absolute;
  right: 16vw;
  top: 8vw;
}
.top-interests-header {
  color: var(--primary-color);
  font-size: 1.8rem;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  margin-bottom: 1rem;
}

.top-interests-follow {
  padding: 10px;
  font-size: 1rem;
