}

.edit-profile-tabbar {
  display: flex;
  width: 70vw !important;
  justify-content: flex-start;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-top: 1rem;
}
.edit-profile-tabbar div {
  padding: 10px;
  border-bottom: 1px solid var(--primary-color);
