  box-shadow: 1px 3px 9px -6px var(--secondary-color) inset,
    0px 15px 3px -2px white;
  border-top-left-radius: 10px !important;
  border-top-right-radius: 10px !important;
  z-index: 2;
  color: var(--primary-color);
}

.tab {
  position: relative;
  top: -2px;
