  margin-bottom: 1.2rem;
  border-radius: 10px;
  box-shadow: 1px 2px 3px 2px var(--secondary-color);
}

.btn-update:hover {
  transform: scale(1.2);
  box-shadow: 1px 0px 2px 3px var(--secondary-color);
}

.Preference-container .Interest-select-item-container {
  margin: 0;
}

.Change-password-container {
  padding: 20px;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  color: #ff5050;
}

.SignUp-form-button input:disabled {
  background-color: rgb(173, 173, 173) !important;
  color: var(--primary-color) !important;
}

.friends-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.empty-friends {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80vw;
  height: 60vh;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  font-size: 1.5rem;
  color: var(--primary-color);
}



.category-title {
  margin: 1.2rem;
  font-size: 3rem;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  text-shadow: 1px 8px 7px grey;
}

.single-post {
  left: 45%;
}

.ShowPost-container {
  margin-top: 3rem;
  position: relative;
}

.ShowPost-add-comment-container {
  width: 45vw;
  padding: 1rem;
  position: relative;
  top: -2.5rem;
  border: 1px solid var(--primary-color);
  border-radius: 8px;
  box-shadow: 0px 0px 12px -7px var(--primary-color) inset;
  margin-bottom: 2.5rem;
}

.Add-comment-container {
  margin-bottom: 1.2rem;
}

.ShowPost-add-comment {
  width: 100%;
  border: none;
  padding: 10px;
  margin-top: 0.3rem;
  font-size: 0.8rem;
  transition: all 0.4s ease-in;
}
.ShowPost-add-comment:hover,
.ShowPost-add-comment:focus {
  outline: none !important;
  border: none !important;
  border-bottom: 1px solid var(--primary-color) !important;
}

.create-post-btn {
  padding: 10px;
  border-radius: 8px;
  text-transform: uppercase;
}

.create-post-btn:hover,
.create-post-btn:focus {
  background-color: var(--primary-color);
  color: white;
  outline: none;
  letter-spacing: 0.3rem;
}

.Add-comment-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.edit-comment {
  float: right;
  cursor: pointer;
}
.edit-comment img,
.comment-stat img {
  width: 13px !important;
  height: 13px !important;
}

.comment-footer {
  display: flex;
  margin-top: 0.5rem;
  color: var(--primary-color);
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  justify-content: flex-end;
  font-size: 0.6rem;
}

.commet {
  margin-bottom: 0.8rem;
  position: relative;
}
.comment-stat {
  color: var(--primary-color);
  font-size: 0.8rem;
  font-feature-settings: "c2sc";
  font-variant: small-caps;
  margin-right: 1.2rem;
  cursor: pointer;
}

.commet-date {
  font-size: 0.3rem;
}

.edit-option-available {
  float: right;
}

.edit-option-available img {
  width: 15px !important;
  height: 15px !important;
}

.Post-container textarea {
  width: 100% !important;
}

